import React from "react";
import "./Popup.css";
import { AiFillCloseCircle } from "react-icons/ai";

export const Popup = (props) => {
  return props.trigger ? (
    <div className="popup">
      <div className="popup-inner">
        <button
          className="close-btn"
          onClick={() => props.setTrigger(false)}
        ></button>
        <AiFillCloseCircle />

        {props.children}
      </div>
    </div>
  ) : (
    ""
  );
};
